@import '../../global/global.css';

:host {
  display: block;
}

/* Content area - can be scrollable or not based on parent needs */
.month-picker-content {
  width: 100%;
  overflow-x: hidden;
  font-family: var(--font-inter);
}

/* Extend animation duration for smoother effect */
.month-picker-content.animate-slide-up-enter {
  animation-duration: var(--animation-duration-slow, 0.3s);
}

.month-picker-content.scrollable {
  max-height: 340px;
  overflow-y: auto;
  overflow-y: overlay; /* Overlay scrollbar - doesn't take space from content */
  position: relative;
}

/* Custom scrollbar styling - hidden by default, visible on hover */
.month-picker-content.scrollable::-webkit-scrollbar {
  width: var(--spacing-xs);
}

.month-picker-content.scrollable::-webkit-scrollbar-track {
  background: transparent;
  border-radius: var(--radius-xs);
}

.month-picker-content.scrollable::-webkit-scrollbar-thumb {
  background: transparent;
  border-radius: var(--radius-xs);
  transition: background 0.2s ease;
}

/* Show scrollbar on hover */
.month-picker-content.scrollable:hover::-webkit-scrollbar-track {
  background: var(--color-neutral-50);
}

.month-picker-content.scrollable:hover::-webkit-scrollbar-thumb {
  background: var(--color-neutral-200);
}

.month-picker-content.scrollable:hover::-webkit-scrollbar-thumb:hover {
  background: var(--color-neutral-300);
}

/* Firefox scrollbar - hidden by default */
.month-picker-content.scrollable {
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
}

/* Firefox - show scrollbar on hover */
.month-picker-content.scrollable:hover {
  scrollbar-color: var(--color-neutral-200) transparent;
}

/* Calendar container */
.calendar {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: var(--spacing-lg) 0 var(--spacing-lg) var(--spacing-lg);
}

/* Year Section - Contains year button + months row */
.year-section {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.year-month-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: flex-start;
}

/* Year Label - positioned on left */
.year-label {
  width: auto;
  height: auto;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  font-family: var(--font-inter);
  font-weight: var(--weight-bold);
  font-size: var(--text-sm-size);
  line-height: var(--text-md-line);
  color: var(--color-neutral-700);
  flex-shrink: 0;
  min-width: 50px;
  padding: var(--spacing-md) 0;
}

/* Months Grid - 2 rows of 6 months each */
.months-row {
  display: flex;
  flex-direction: column;
  /* flex: 1;
  width: 100%; */
}

/* Month Button - 40x40px circular */
.month-button {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-full);
  border: 2px solid transparent;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: var(--font-inter);
  font-weight: var(--weight-medium);
  font-size: var(--text-sm-size);
  line-height: var(--leading-sm);
  color: var(--color-neutral-500);
  cursor: pointer;
  transition: all var(--animation-duration-fast, 0.1s) var(--animation-easing-ease, ease);
  flex-shrink: 0;
  text-align: center;
  overflow: hidden;
}

.month-row {
  display: flex;
  gap: var(--spacing-sm);
  justify-content: flex-start;
}

.month-button:hover:not(.inactive):not(.selected):not(:disabled):not(.today) {
  background: var(--color-neutral-50);
  color: var(--color-neutral-800);
}

/* Selected state - blue background */
.month-button.selected {
  background: var(--color-brand-600);
  color: var(--color-white);
  font-weight: var(--weight-semibold);
}

/* Today's date state - blue border to match date picker */
.month-button.today:not(.selected) {
  border-color: var(--color-brand-600);
}

.month-button.today:not(.selected):hover {
  background: var(--color-brand-100);
  color: var(--color-brand-600);
}

/* Inactive/disabled state */
.month-button.inactive,
.month-button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
  color: var(--color-neutral-400);
}

.month-button:disabled:hover {
  background: transparent;
}

/* Section Divider - Between year sections */
.section-divider {
  width: 100%;
  margin: var(--spacing-xl) 0;
}

.divider-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.divider-line {
  width: 100%;
  height: 1px;
  background: var(--color-neutral-200);
}

/* Focus states for accessibility - completely disable outline */
.month-button:focus {
  outline: none;
}

/* Keyboard navigation state - will be added via JavaScript */
.month-button.keyboard-focused {
  outline: var(--border-width-md, 2px) solid var(--color-brand-500);
  outline-offset: var(--spacing-xs);
}
