import { Component, Prop, State, Event, EventEmitter, Element, Watch, h, Host } from '@stencil/core';

export interface MonthYear {
  month: number; // 0-11 (January = 0, December = 11)
  year: number;
}

export interface DisabledMonth {
  month: number;
  year: number;
}

@Component({
  tag: 'bh-month-picker-content',
  styleUrl: 'bh-month-picker-content.css',
  shadow: true,
})
export class BhMonthPickerContent {
  @Element() el!: HTMLElement;

  /**
   * Minimum year in the picker
   */
  @Prop() minYear: number = 1998;

  /**
   * Maximum year in the picker
   */
  @Prop() maxYear: number = 2060;

  /**
   * Whether the picker is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Array of disabled months
   */
  @Prop() disabledMonths: DisabledMonth[] = [];

  /**
   * Whether the content is scrollable
   */
  @Prop() scrollable: boolean = true;

  /**
   * Currently selected month (0-11)
   */
  @Prop() selectedMonth?: number;

  /**
   * Currently selected year
   */
  @Prop() selectedYear?: number;

  /**
   * Height of the content area
   */
  @Prop() height: string = '375px';

  /**
   * Value object with month and year
   */
  @Prop() value?: MonthYear;

  @State() internalSelectedMonth: number | null = null;
  @State() internalSelectedYear: number | null = null;
  @State() isUsingKeyboard: boolean = false;

  /**
   * Event emitted when a month is selected
   */
  @Event() bhChange!: EventEmitter<MonthYear>;

  /**
   * Event emitted when a month/year is selected
   */
  @Event() bhMonthYearSelect!: EventEmitter<MonthYear>;

  private months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  private currentDate = new Date();
  private currentMonth = this.currentDate.getMonth();
  private currentYear = this.currentDate.getFullYear();

  @Watch('value')
  handleValueChange(newValue: MonthYear | undefined) {
    if (newValue) {
      this.internalSelectedMonth = newValue.month;
      this.internalSelectedYear = newValue.year;
    }
  }

  @Watch('selectedMonth')
  handleSelectedMonthChange(newValue: number | undefined) {
    if (newValue !== undefined) {
      this.internalSelectedMonth = newValue;
    }
  }

  @Watch('selectedYear')
  handleSelectedYearChange(newValue: number | undefined) {
    if (newValue !== undefined) {
      this.internalSelectedYear = newValue;
    }
  }

  componentWillLoad() {
    // Initialize with props or current date
    if (this.value) {
      this.internalSelectedMonth = this.value.month;
      this.internalSelectedYear = this.value.year;
    } else if (this.selectedMonth !== undefined || this.selectedYear !== undefined) {
      this.internalSelectedMonth = this.selectedMonth ?? this.currentMonth;
      this.internalSelectedYear = this.selectedYear ?? this.currentYear;
    } else {
      // Initialize with current date if within range
      if (this.currentYear >= this.minYear && this.currentYear <= this.maxYear) {
        this.internalSelectedMonth = this.currentMonth;
        this.internalSelectedYear = this.currentYear;
      } else {
        this.internalSelectedMonth = this.currentMonth;
        this.internalSelectedYear = this.currentYear < this.minYear ? this.minYear : this.maxYear;
      }
    }
  }

  componentDidLoad() {
    if (this.scrollable) {
      setTimeout(() => this.scrollToSelectedYear(), 0);
    }
  }

  private scrollToSelectedYear() {
    if (this.internalSelectedYear === null) return;

    const container = this.el.shadowRoot?.querySelector('.month-picker-content') as HTMLElement;
    const yearElement = this.el.shadowRoot?.querySelector(`#year-${this.internalSelectedYear}`) as HTMLElement;

    if (!container || !yearElement) return;

    const containerHeight = container.clientHeight;
    const yearElementTop = yearElement.offsetTop;
    const yearElementHeight = yearElement.offsetHeight;

    const isFirstYear = this.internalSelectedYear === this.minYear;
    const isLastYear = this.internalSelectedYear === this.maxYear;

    if (!isFirstYear && !isLastYear) {
      const scrollOffset = 50;
      const scrollTop = yearElementTop - containerHeight / 2 + yearElementHeight / 2 + scrollOffset;
      container.scrollTop = Math.max(0, scrollTop);
    } else if (isFirstYear) {
      container.scrollTop = 0;
    } else {
      container.scrollTop = container.scrollHeight;
    }
  }

  private getYears(): number[] {
    return Array.from({ length: this.maxYear - this.minYear + 1 }, (_, i) => this.minYear + i);
  }

  private isCurrentMonth(monthIndex: number, year: number): boolean {
    return this.currentMonth === monthIndex && this.currentYear === year;
  }

  private isMonthSelectable(monthIndex: number, year: number): boolean {
    if (year < this.minYear || year > this.maxYear) {
      return false;
    }

    return !this.disabledMonths.some(
      disabled => disabled.month === monthIndex && disabled.year === year
    );
  }

  private selectMonth(monthIndex: number, year: number) {
    if (this.disabled || !this.isMonthSelectable(monthIndex, year)) {
      return;
    }

    this.internalSelectedMonth = monthIndex;
    this.internalSelectedYear = year;

    const selection: MonthYear = { month: monthIndex, year };
    this.bhChange.emit(selection);
    this.bhMonthYearSelect.emit(selection);
  }

  private handleKeyDown = () => {
    this.isUsingKeyboard = true;
  };

  private handleMouseDown = () => {
    this.isUsingKeyboard = false;
  };

  private getMonthButtonClasses(monthIndex: number, year: number): string {
    const classes = ['month-button'];

    if (!this.isMonthSelectable(monthIndex, year)) {
      classes.push('inactive');
    }
    if (this.internalSelectedMonth === monthIndex && this.internalSelectedYear === year) {
      classes.push('selected');
    }
    if (this.isCurrentMonth(monthIndex, year)) {
      classes.push('today');
    }

    return classes.join(' ');
  }

  render() {
    const years = this.getYears();

    return (
      <Host>
        <div
          class={`month-picker-content animate-slide-up-enter ${this.scrollable ? 'scrollable' : ''}`}
          style={{ height: this.height }}
          onKeyDown={this.handleKeyDown}
          onMouseDown={this.handleMouseDown}
        >
          <div class="calendar">
            {years.map((year, yearIndex) => (
              <div key={year} class="year-section" id={`year-${year}`}>
                <div class="year-month-container">
                  <div class="year-label">{year}</div>

                  <div class="months-row">
                    {/* First row: Jan-Jun */}
                    <div class="month-row">
                      {this.months.slice(0, 6).map((_, monthIndex) => (
                        <button
                          key={monthIndex}
                          class={this.getMonthButtonClasses(monthIndex, year)}
                          disabled={!this.isMonthSelectable(monthIndex, year)}
                          onClick={() => this.selectMonth(monthIndex, year)}
                        >
                          {this.months[monthIndex]}
                        </button>
                      ))}
                    </div>

                    {/* Second row: Jul-Dec */}
                    <div class="month-row">
                      {this.months.slice(6, 12).map((_, idx) => {
                        const monthIndex = idx + 6;
                        return (
                          <button
                            key={monthIndex}
                            class={this.getMonthButtonClasses(monthIndex, year)}
                            disabled={!this.isMonthSelectable(monthIndex, year)}
                            onClick={() => this.selectMonth(monthIndex, year)}
                          >
                            {this.months[monthIndex]}
                          </button>
                        );
                      })}
                    </div>
                  </div>
                </div>

                {/* Section Divider (not for last year) */}
                {yearIndex < years.length - 1 && (
                  <div class="section-divider">
                    <div class="divider-container">
                      <div class="divider-line"></div>
                    </div>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </Host>
    );
  }
}
